import React, { Component, lazy } from 'react'
import { Redirect } from 'react-router';
import {
  CBadge,
  CDataTable,
  CFormGroup,
  CLabel,
  CInput,
  CButton,
  CForm,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CInputCheckbox,
  CCol,
  CRow,
} from '@coreui/react'
import {fieldviavalen, jns_manifest, jns_moda, datakpbcs, kelpos } from './Datasource.js'
import Detail from './Detail.js'

class Viavalen extends Component {
  constructor(props) {
    super(props);
    this.state = {
    redirect: false,
    dataentrymanifest : [{"no": "1", "no_layanan": "12345678910", "jns_manifest": "IS - Inward Manifest Laut", "status":"Draft", "wkt_proses":"01-04-2020 11:43:55 AM"}, {"no": "1", "no_layanan": "0987654321", "jns_manifest": "IS - Inward Manifest Laut", "status":"Terkirim", "wkt_proses":"01-04-2020 11:43:55 AM"}]
    };
  }
  handleOnClick = () => {
    // some action...
    // then redirect
    this.setState({redirect: true});
  }
  render(){
      const Getbadge = status => {
        switch (status) {
          case 'Draft': return 'success'
          case 'Terkirim': return 'info'
        }
      }
      if (this.state.redirect) {
        return <Redirect push to="/nvocc/input" />;
      }
      console.log("status state", this.state.id)
      console.log("status nomaster", this.state)  
    return(
      <CCard>
        <CCardHeader className="bg-light text-dark" >
          <h5>Daftar Pengajuan Entry Manifest - NVOCC (Via Valen)</h5>
        </CCardHeader>      
        <CCardBody>
          <div className="p-2 d-flex justify-content-end">
          <CRow>
            <CButton onClick={this.handleOnClick.bind(this)} color="info" >Tambah</CButton>
          </CRow>
          </div>            
          <CRow>
            <CDataTable
            itemsPerPage={3}
            items={this.state.dataentrymanifest}
            fields={fieldviavalen}
            pagination
            columnFilter
            scopedSlots = {
                {
                'status':
                  (item)=>(
                    <td>
                      <CBadge color={Getbadge(item.status)}>
                        {item.status}
                      </CBadge>
                    </td>
                  ),
                'action' :
                (item,index) => {
                return (
                    <td>
                    <CButton size="sm" color="success" >Edit</CButton>{' '}
                    <CButton size="sm" color="info" >Lihat</CButton>{' '}
                    <CButton size="sm" color="danger" >Hapus</CButton>
                    </td>
                    )
                },  
                }
            }
            />
          </CRow>                   
        </CCardBody>
      </CCard>
      )
  }  
}

export default Viavalen
